if (!isMove && Date.now() - startTime < 150) {




_this.config()
}


isMove = false;
startTime = 0;
});
}*/
},
toSeach(){
this.$router.push('/mSearch')
},
onSwiperItemIndexChange(){

},

mobileReload(){
this.getBanner()
this.getRecentEx()
this.getHightEx()
this.getStory()
},

getBanner(){
this.$http.post(this.api + '/home/Banner/bannerlist',{
token: localStorage.getItem('token'),
flag: 0
})
.then(res=>{
if(res.data.code == 1){
this.bannerList = res.data.data
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getBanner()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},

getHeight(){
var height = window.screen.height
var width = window.screen.width
this.margin = (width - (60*4))/8
this.hieght = height * 0.3 /10
},

tochWap(){
let _this = this

var wrap = document.getElementById('wrap')
_this.width = 250*_this.recentList.length
var startPoint = 0;
var startEle = 0;

wrap.addEventListener("touchstart",function(e){

startPoint = e.changedTouches[0].pageX;
startEle = _this.left
});

wrap.addEventListener("touchmove",function(e){
var currPoint = e.changedTouches[0].pageX;
var disX = currPoint - startPoint;
var left = startEle + disX;
if(left <= (230- _this.width)){
_this.left = (230- _this.width)
}else if(left > 0){
_this.left = 0
}else{
_this.left = left
}
});
},

getRecentEx(){
this.$http.post(this.api + '/SoonAct',{
token: localStorage.getItem('token')
})
.then(res=>{
if(res.data.code == 1){
this.recentList = res.data.data.data
this.tochWap()

}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getRecentEx()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},

getHightEx(){
this.$http.post(this.api + '/home/Activity/activ_list',{
token: localStorage.getItem('token'),
sort:1,
page:1
})
.then(res=>{
if(res.data.code == 1){
this.highgScore = res.data.data.data
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getHightEx()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},

getStory(){
this.$http.post(this.api + '/home/Story/story_list',{
token: localStorage.getItem('token'),
sort:2,
page: 1
})
.then(res=>{
if(res.data.code == 1){
this.storyList = res.data.data.data
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getStory()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},

toPage(item,index){
if(index == 6){
MessageBox.alert('请前往客户端或者APP查看更多内容')
}else if(index == 5){
if(localStorage.getItem('isLogin')){
this.$router.push(item.router)
}else{
MessageBox.alert('请登录查看最近浏览')
}
}else{
this.$router.push(item.router)
}
},
toPublish(item,index){
this.$router.push({
path: '/mPublishPage',
query:{
information: item.activity_id
}
})
},
toStory(item,index){
this.$router.push({
path: '/mStorypage',
query:{
information: item.story_id
}
})
},
LookMore(val){
if(val == 1){
this.activeIndex += 1
}else{
this.storyIndex += 1
}
this.getMore(val)
},
getMore(val){
if(val == 1){
this.$http.post(this.api + '/home/Activity/activ_list',{
token: localStorage.getItem('token'),
sort:1,
page:this.activeIndex
})
.then(res=>{
if(res.data.code == 1){
if(res.data.data.data.length){
for(var i = 0;i< res.data.data.data.length;i++){
this.highgScore.push(res.data.data.data[i])
}
}else{
MessageBox.alert('没有更多体验了哟！')
}
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getMore()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
}else{
this.$http.post(this.api + '/home/Story/story_list',{
token: localStorage.getItem('token'),
sort:2,
page: this.storyIndex
})
.then(res=>{
if(res.data.code == 1){
if(res.data.data.data.length){
for(var i = 0; i < res.data.data.data.length;i++){
this.storyList.push(res.data.data.data[i])
}
}else{
MessageBox.alert('没有更多故事了哟！')
}
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getMore()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
}
}
},
mounted(){
this.getHeight()
},
created(){
window.scroll(0,0)
}
}



